*{margin:0;padding:0;box-sizing:border-box}body{background:#16181b;background-attachment:fixed;color:#b1bdb4;font-family:'Roboto', sans-serif;letter-spacing:0.1px}::-webkit-scrollbar{width:0px}.header{display:flex;justify-content:space-between;align-items:center;padding:1rem 3rem;background-color:#16181b;height:10vh;width:100%;position:fixed;top:0;z-index:999}.header__menu{display:none}.header__logo{width:50px;height:50px;object-fit:contain;display:block}.header form{flex:0.6 1;display:flex;padding:0.1rem;margin:0.1rem;border-radius:3px;border:1.2px solid;background-color:#121417}.header form input{width:100%;border:none;font-weight:500;background:transparent;padding:0.3rem;color:#b1bdb4}.header form input:focus{outline:none}.header form button{padding:0 1.25rem;color:#b1bdb4;background:transparent;border:none}.header form button:focus{border:none}.header__icon{flex:0.15 1;display:flex;justify-content:space-around;align-items:center}.header__icon button{padding:0 1.25rem;background:transparent;border:none}.header__icon button:focus{border:none}@media (max-width: 520px){.header{padding:1rem}.header form{flex:1 1}.header__menu{display:block}.header__logo{display:none}.header__icons>*:not(button){display:none}}

*{margin:0;padding:0;box-sizing:border-box}body{background:#16181b;background-attachment:fixed;color:#b1bdb4;font-family:'Roboto', sans-serif;letter-spacing:0.1px}::-webkit-scrollbar{width:0px}.sidebar{background:#121417;display:flex;flex-direction:column;width:250px;height:90vh;padding-top:2rem;transition:-webkit-transform 0.2s ease-in;transition:transform 0.2s ease-in;transition:transform 0.2s ease-in, -webkit-transform 0.2s ease-in;position:sticky;top:10vh;left:0}.sidebar a{color:#b1bdb4}.sidebar a:hover{color:#b1bdb4;text-decoration:none}.sidebar li{display:flex;align-items:center;padding:0.6rem 1.5rem;margin:0.2rem 0;cursor:pointer}.sidebar li span{margin-left:1rem;font-size:14px;font-weight:500;letter-spacing:0.4px}.sidebar li:hover{background-color:#4c4c4c}.sidebar hr{background-color:#4c4c4c}.sidebar.open{-webkit-transform:translateX(0);transform:translateX(0)}@media (max-width: 1224px){.sidebar{width:90px}.sidebar li{justify-content:center}.sidebar span{display:none}}@media (max-width: 520px){.sidebar{-webkit-transform:translateX(-100%);transform:translateX(-100%);position:fixed;z-index:999}}

*{margin:0;padding:0;box-sizing:border-box}body{background:#16181b;background-attachment:fixed;color:#b1bdb4;font-family:'Roboto', sans-serif;letter-spacing:0.1px}::-webkit-scrollbar{width:0px}.video{margin-bottom:1rem;padding:0.7rem;font-weight:500;font-size:0.9rem;cursor:pointer}.video__top{margin-bottom:0.5rem;position:relative}.video__top .lazy-load-image-background{width:100%}.video__top img{width:100%}.video__top__duration{position:absolute;bottom:0.3rem;right:0.3rem;padding:0.2rem;background:#080808ec;border-radius:3px}.video__title{margin-bottom:0.1rem;color:#fff;overflow:hidden;display:-webkit-box;-webkit-line-clamp:1;-webkit-box-orient:vertical}.video__details{display:flex;align-items:center}.video__channel{display:flex;align-items:center;margin:0.5rem 0}.video__channel img{width:36px;height:36px;border-radius:50%;margin-right:0.5rem;cursor:pointer}.video__channel p{margin-bottom:0}

*{margin:0;padding:0;box-sizing:border-box}body{background:#16181b;background-attachment:fixed;color:#b1bdb4;font-family:'Roboto', sans-serif;letter-spacing:0.1px}::-webkit-scrollbar{width:0px}.categoriesBar{padding:0.5rem 0;font-size:0.8rem;display:flex;overflow-x:scroll;scrollbar-width:none}.categoriesBar::-webkit-scrollbar{width:0px}.categoriesBar span{margin-right:1rem;padding:0.5rem;white-space:nowrap;border:1.5px solid #b1bdb4;border-radius:999px;cursor:pointer}.categoriesBar span:hover{background-color:#374a59}.categoriesBar span.active{color:#fff;background-color:#606060;border-color:#4c4c4c}

*{margin:0;padding:0;box-sizing:border-box}body{background:#16181b;background-attachment:fixed;color:#b1bdb4;font-family:'Roboto', sans-serif;letter-spacing:0.1px}::-webkit-scrollbar{width:0px}.login{height:100vh;display:grid;place-items:center}.login__container{background-color:#121417;padding:2rem;margin:0 1rem;border-radius:10px;display:flex;flex-direction:column;align-items:center}.login__container button{padding:0.5rem;border:none;border-radius:5px;margin-bottom:1rem}.login__container button:focus{outline:none}.login img{width:130px;height:130px;object-fit:contain}

